import { Component, Input } from '@angular/core';
import { TranslationsService } from '@backbase/accessgroup-common-ang';

@Component({
  selector: 'bb-business-function-group-name',
  templateUrl: './business-function-group-name.component.html',
})
export class BusinessFunctionGroupNameComponent {
  /**
   * Business function groupKey for which the name is shown
   */
  @Input() groupKey: string | undefined;

  constructor(private translationsService: TranslationsService) {}

  get groupName(): string | undefined {
    if (!this.groupKey) return '';
    return this.translationsService.getBusinessFunctionGroupName(this.groupKey) || this.groupKey;
  }
}
